
export interface IDialogOptions {
    title?: string;
    image?: { src: string, width?: string, height?: string; };
    closable?: boolean;
    type?: "warning";   
    //  | "info" | "success" | "confirm" ilave olarak dier typelar konuulup eklenmeli
    width?: string;
    height?: string;
    maxHeight?:string;
    overflow?: string;
    closeCb?: (reason: "close" | "outclick" | "component") => void;
    noBlockShadow?: boolean;
    closeOnOutClick?: boolean;
    customStyle? : {
        closeIcon?: string;
    }
}




export interface IDialog {
    showDialog<ComponentParams>(component: object, options?: IDialogOptions, parameters?: ComponentParams): string;
    closeDialog(id: string): void;
    closeAllDialogs(): void;
}
export const IDialog = Symbol.for("IDialog");