import { container, singleton, inject, injectable } from "tsyringe";
import { DependencyContainer } from "tsyringe";
import { createPromiseData } from "../../common/promiseHelper";

interface IDependencyContainer extends DependencyContainer { }; // for vite vs webpack :( remember

function sleep() {
    const prom = createPromiseData<void>();
    setTimeout(() => { prom.resolver() }, 100);
    return prom.promise;
}

export async function delayWithResolve<TInterface>(dependency: symbol, cb: (dep: TInterface) => void | Promise<void>) {
    await delayTillResolve([dependency]);
    const dep = container.resolve<TInterface>(dependency);
    await cb(dep);
}
export async function delayTillResolve(dependencies: Array<symbol>, cb?: () => void | Promise<void>) {
    let run = false;
    while (!run) {
        try {
            dependencies.forEach(dependency => container.resolve(dependency));
            run = true;
        } catch (err) {
            await sleep();
        }
    }
    await cb?.();
}

export { container, singleton, inject, injectable, IDependencyContainer }